import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios' 
import'mint-ui/lib/style.css'

Vue.config.productionTip = false
// 0.1:Vuex
import Vuex from "vuex"
// 0.2Vuex
Vue.use(Vuex)
// 0.3store
var store=new Vuex.Store({
  // 
  state:{
    cartCount:sessionStorage.getItem("cartCount")||0,
    uid:sessionStorage.getItem("uid")||''
    // product_sum:0
  },
  //
  mutations:{
    increment(state){
      state.cartCount++;
      sessionStorage.setItem("cartCount",state.cartCount)
    }
  },
  //
  getters:{
    optCartCount:function(state){
      return state.cartCount;
    }
  }
  // 
  // {{$store.getters.optCartCount}}
  // 
  // this.c1=this.$store.state.fj1;
})
// 4storeVuex
axios.defaults.withCredentials=true;
Vue.prototype.axios=axios;
//5:  
//6:post  application/x-www-form-urlencoded
import './lib/mui/css/mui.css'
//  MUI 
// 
import './lib/mui/css/icons-extra.css'

// 1header
import { Header,Button,Search,Swipe,SwipeItem, Popup } from "mint-ui"
Vue.component(Popup.name, Popup);
// 1.1header
Vue.component(Header.name,Header);
//2button
//2.1button
Vue.component(Button.name,Button);
//3
//3.1search
Vue.component(Search.name,Search)
//4
//4.1Swipe
Vue.component(Swipe.name,Swipe)
//5
//5.1SwipeItem
Vue.component(SwipeItem.name,SwipeItem);

//6.1
Vue.filter("dateFilter",function(value){
    var now=new Date(value);
    var y=now.getFullYear();
    var m=now.getMonth()+1;
    var d=now.getDate();
    var h=now.getHours();
    var mi=now.getMinutes();
    var s=now.getSeconds();
    if(m<10){
      m="0"+m;
    }
    if(d<10){
      d="0"+d;
    }
    if(h<10){
      h="0"+h;
    }
    if(mi<10){
      mi="0"+mi;
    }
    if(s<10){
      s="0"+s;
    }
  return `${y}-${m}-${d} ${h}:${mi}:${s}`
  // .toLocaleString()
})

//7.main.js    qs
//axios post
import qs from "qs";
Vue.prototype.qs=qs;
new Vue({
  router,
  render: h => h(App),
  store   //0.4 storeVue
}).$mount('#app')



